"use client";

import { useState, useEffect, useMemo, Fragment } from "react";
import axios from "axios";
import Fuse from "fuse.js";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Dialog, Transition } from "@headlessui/react";
import { motion } from "framer-motion";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";

// Define the Course interface
interface Course {
  courseReferenceNumber: string;
  courseTitle: string;
  courseMajor: string;
  subjectCourse: string;
  faculty?: { displayName: string }[];
  meetingsFaculty?: {
    meetingTime: {
      beginTime: string;
      endTime: string;
      monday: boolean;
      tuesday: boolean;
      wednesday: boolean;
      thursday: boolean;
      friday: boolean;
      buildingDescription?: string;
      room?: string;
    };
  }[];
}

export default function Home() {
  const [courses, setCourses] = useState<Course[]>([]);
  const [selectedCourses, setSelectedCourses] = useState<Course[]>([]);
  const [search, setSearch] = useState("");
  const [theme, setTheme] = useState("light");
  const [universityTheme, setUniversityTheme] = useState("default");

  // University theme configurations
  const universityThemes = {
    default: {
      primary: "from-blue-600 to-purple-600",
      primaryDark: "from-blue-400 to-purple-400",
      accent: "blue",
      courseColors: ["#3B82F6", "#8B5CF6", "#EC4899", "#10B981", "#F59E0B", "#EF4444", "#6366F1"],
    },
    richmond: {
      primary: "from-[#003057] to-[#C41E3A]",
      primaryDark: "from-[#003057] to-[#C41E3A]",
      accent: "blue",
      courseColors: ["#003057", "#C41E3A", "#4A90E2", "#E74C3C", "#2C3E50", "#E67E22", "#27AE60"],
    },
    georgiatech: {
      primary: "from-[#B3A369] to-[#003057]",
      primaryDark: "from-[#B3A369] to-[#003057]",
      accent: "yellow",
      courseColors: ["#B3A369", "#003057", "#FFD700", "#2C3E50", "#E67E22", "#27AE60", "#8E44AD"],
    },
    virginia: {
      primary: "from-[#232D4B] to-[#E57200]",
      primaryDark: "from-[#232D4B] to-[#E57200]",
      accent: "orange",
      courseColors: ["#232D4B", "#E57200", "#FFA726", "#2C3E50", "#66BB6A", "#42A5F5", "#EC407A"],
    },
    duke: {
      primary: "from-[#012169] to-[#CC0000]",
      primaryDark: "from-[#012169] to-[#CC0000]",
      accent: "blue",
      courseColors: ["#012169", "#CC0000", "#4A90E2", "#E74C3C", "#2C3E50", "#E67E22", "#27AE60"],
    },
  };

  // Fetch courses from the backend
  useEffect(() => {
    axios
      .get("http://127.0.0.1:5001/api/courses")
      .then((res) => setCourses(res.data))
      .catch((err) => console.error(err));
  }, []);

  // Toggle dark/light theme
  useEffect(() => {
    document.documentElement.classList.toggle("dark", theme === "dark");
  }, [theme]);

  // Function to add or remove courses from the selected list
  const toggleCourse = (course: Course) => {
    setSelectedCourses((prev) =>
      prev.find((c) => c.courseReferenceNumber === course.courseReferenceNumber)
        ? prev.filter((c) => c.courseReferenceNumber !== course.courseReferenceNumber)
        : [...prev, course]
    );
  };

  // Helper function to get a color from the current theme
  function getThemeColor(index: number) {
    return universityThemes[universityTheme as keyof typeof universityThemes].courseColors[
      index % universityThemes[universityTheme as keyof typeof universityThemes].courseColors.length
    ];
  }

  // ------------------ Color Mapping ------------------
  // Using useMemo to create a mapping of courseReferenceNumber to a theme color
  const courseColors = useMemo(() => {
    const mapping: Record<string, string> = {};
    courses.forEach((course, index) => {
      mapping[course.courseReferenceNumber] = getThemeColor(index);
    });
    return mapping;
  }, [courses, universityTheme]);
  // ---------------------------------------------------

  // Create a Fuse.js instance for fuzzy search on multiple keys
  const fuse = new Fuse(courses, {
    keys: [
      "courseTitle",
      "courseMajor",
      "subjectCourse",
      {
        name: "faculty",
        getFn: (course: Course) =>
          course.faculty?.map((f) => {
            const parts = f.displayName.split(", ");
            return parts.length === 2 ? `${parts[1]} ${parts[0]}` : f.displayName;
          }) || [],
      },
    ],
    threshold: 0.4,
  });

  // Use Fuse.js for fuzzy filtering of courses
  const filteredCourses = search.trim()
    ? fuse.search(search).map((result) => result.item)
    : courses;

  // Convert selected courses into FullCalendar events, using consistent colors from courseColors
  const events = selectedCourses.flatMap((course) =>
    course.meetingsFaculty?.map((meeting) => {
      if (!meeting.meetingTime) return null;

      // Get the consistent random color for this course
      const randomColor = courseColors[course.courseReferenceNumber];

      const { beginTime, endTime, monday, tuesday, wednesday, thursday, friday } = meeting.meetingTime;
      const days = [monday, tuesday, wednesday, thursday, friday];

      return days
        .map((day, index) =>
          day
            ? {
                title: course.courseTitle,
                startTime: beginTime ? `${beginTime.substring(0, 2)}:${beginTime.substring(2)}` : "00:00",
                endTime: endTime ? `${endTime.substring(0, 2)}:${endTime.substring(2)}` : "00:00",
                daysOfWeek: [index + 1], // 1 = Monday, 2 = Tuesday, etc.
                backgroundColor: randomColor,  // Use the pre-assigned random color
                borderColor: randomColor,
                textColor: "#fff",
              }
            : null
        )
        .filter(Boolean);
    })
  ).flat();

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800 p-8 transition-all">
      <div className="max-w-7xl mx-auto">
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className={`text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r ${universityThemes[universityTheme as keyof typeof universityThemes].primary} dark:${universityThemes[universityTheme as keyof typeof universityThemes].primaryDark}`}>
              Blink
            </h1>
            <p className="text-gray-600 dark:text-gray-400 mt-1">Create your perfect schedule</p>
          </div>
          <div className="flex items-center space-x-4">
            <select
              value={universityTheme}
              onChange={(e) => setUniversityTheme(e.target.value)}
              className="px-4 py-2 rounded-xl bg-white dark:bg-gray-800 border-2 border-gray-200 dark:border-gray-700 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 dark:focus:ring-blue-800 transition-all duration-200"
            >
              <option value="default">Default Theme</option>
              <option value="richmond">University of Richmond</option>
              <option value="georgiatech">Georgia Tech</option>
              <option value="virginia">University of Virginia</option>
              <option value="duke">Duke University</option>
            </select>
            <button
              onClick={() => setTheme(theme === "light" ? "dark" : "light")}
              className="p-3 rounded-full bg-white dark:bg-gray-800 shadow-lg hover:shadow-xl transition-all duration-200"
            >
              {theme === "light" ? "🌙" : "☀️"}
            </button>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Sidebar - Course Selection */}
          <div className="lg:col-span-1">
            <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-xl p-6 h-[calc(100vh-12rem)] overflow-hidden flex flex-col">
              {/* Sidebar Content */}
              <div className="flex-1 overflow-hidden">
                <div className="flex flex-col h-full">
                  <div className="p-4">
                    <div className="relative">
                      <input
                        type="text"
                        placeholder="Search courses..."
                        className="w-full p-4 pl-12 rounded-xl border-2 border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 dark:focus:ring-blue-800 transition-all duration-200 text-sm font-medium placeholder-gray-400"
                        onChange={(e) => setSearch(e.target.value)}
                      />
                      <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                        <svg
                          className="h-5 w-5 text-gray-400"
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                        >
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                        </svg>
                      </div>
                    </div>
                  </div>

                  <div className="flex-1 overflow-y-auto px-4">
                    <div className="space-y-4">
                      {filteredCourses.map((course) => {
                        const facultyNames =
                          course.faculty
                            ?.map((f) => {
                              const parts = f.displayName.split(", ");
                              return parts.length === 2 ? `${parts[1]} ${parts[0]}` : f.displayName;
                            })
                            .join(", ") || "TBA";
                        const location =
                          course.meetingsFaculty?.[0]?.meetingTime?.buildingDescription
                            ? `${course.meetingsFaculty[0].meetingTime.buildingDescription} - ${course.meetingsFaculty[0].meetingTime.room}`
                            : "TBA";

                        return (
                          <motion.div
                            key={course.courseReferenceNumber}
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            whileHover={{ scale: 1.02 }}
                            whileTap={{ scale: 0.98 }}
                          >
                            <Card
                              onClick={() => toggleCourse(course)}
                              className={`cursor-pointer transition-all duration-200 ${
                                selectedCourses.find((c) => c.courseReferenceNumber === course.courseReferenceNumber)
                                  ? `border-2 border-${universityThemes[universityTheme as keyof typeof universityThemes].accent}-500 shadow-lg bg-${universityThemes[universityTheme as keyof typeof universityThemes].accent}-50 dark:bg-${universityThemes[universityTheme as keyof typeof universityThemes].accent}-900/20`
                                  : "hover:shadow-md hover:border-gray-300 dark:hover:border-gray-600"
                              }`}
                            >
                              <CardHeader className="pb-2">
                                <CardTitle className="text-lg font-semibold">{course.courseTitle}</CardTitle>
                              </CardHeader>
                              <CardContent className="space-y-2">
                                <div className="flex items-center space-x-2 text-sm text-gray-600 dark:text-gray-400">
                                  <span className="px-2 py-1 bg-gray-100 dark:bg-gray-700 rounded-full">
                                    {course.courseMajor}
                                  </span>
                                </div>
                                <div className="text-sm space-y-1">
                                  <p className="text-gray-500 dark:text-gray-400">CRN: {course.courseReferenceNumber}</p>
                                  <p className="text-gray-500 dark:text-gray-400">Faculty: {facultyNames}</p>
                                  <p className="text-gray-500 dark:text-gray-400">Location: {location}</p>
                                </div>
                              </CardContent>
                            </Card>
                          </motion.div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Right Content - Calendar */}
          <div className="lg:col-span-2">
            <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-xl p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl font-semibold text-gray-800 dark:text-white">Weekly Schedule</h2>
                <button
                  className={`px-4 py-2 bg-${universityThemes[universityTheme as keyof typeof universityThemes].accent}-500 text-white rounded-xl hover:bg-${universityThemes[universityTheme as keyof typeof universityThemes].accent}-600 transition-colors duration-200 flex items-center space-x-2`}
                  onClick={() => {/* TODO: Implement export to ICS */}}
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
                  </svg>
                  <span>Export to Calendar</span>
                </button>
              </div>
              <FullCalendar
                plugins={[dayGridPlugin, timeGridPlugin]}
                initialView="timeGridWeek"
                events={events}
                allDaySlot={false}
                slotMinTime="07:00:00"
                slotMaxTime="22:00:00"
                headerToolbar={{
                  left: 'prev,next today',
                  center: 'title',
                  right: 'timeGridWeek,timeGridDay'
                }}
                height="auto"
                className="rounded-xl overflow-hidden"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}